.App {
  text-align: center;
  font-family: Holtwood One SC;
}
body {
  font-family: holtwood one sc;
  margin: 0;
  padding: 0;
}
@font-face {
  font-family: 'Holtwood One SC';
  src: url("https://fonts.google.com/specimen/Holtwood+One+SC");
  src: local('Holtwood One SC'), url(./fonts/Holtwood_One_SC/HoltwoodOneSC-Regular.ttf) format('opentype');
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
